<template>
  <div id="app">
    <app-header></app-header>
    <router-view/>
  </div>
</template>

<script>
import Header from './components/Header'
export default {
  components:{
    'app-header':Header
  }
}
</script>

<style lang="scss">

body{
  position: relative;
}

.error , .success{
  text-align: center;
  padding: 0.45rem;
  margin-top:0.75rem;
  border-radius: 3px;
  color:#fff;
  margin-bottom: 0.5rem;
}
.error {background: rgba(red , .6);}
.success {background: rgba(green , .6)}
@media screen and (max-width:414px){
    .submit , .delete{
      flex-basis: unset !important;
      min-width: 100%;
    }
    .modal_content{
      max-width: 75% !important;
    }
}
@media screen and (max-width:736px){
    .modal_content{
      max-height: 75% !important;
      max-width: 75% !important;
    }
}
</style>



