<template>
  <div class="main">
    <form @submit.prevent="addMessage()">
        
        <div class="input-field flex-input">
            <input type="text" placeholder="Your Message..." class="main_input" v-model="text" @focus="scroll">
             <emoji/>
        </div>
        <div class="btn-group">
            <button type="submit" class="submit btn green button">Send Message
                <i class="material-icons prefix">send</i>
            </button>
            <button type="button" class="delete btn red button" 
                    :disabled="issetMessage == 0 ? true : false"
                    @click="deleteAllData()" >Delete message
                <i class="material-icons prefix">delete</i>
            </button>
        </div>
       
    </form>
    
  </div>
</template>

<script>

import db from '../firebase/init.js'
import util from '../utils/util';
import Emoji from '../components/Emoji'

/**
 * Creatd By @author Robert Aslanyan
 * May 2018 
 *  
 */

export default { 
    props:['name' , 'content' , 'issetMessage'],
    components:{
        Emoji
    },
    data(){
        return{
            text:null,
        }
    },
    methods:{
        scroll(){
            return util.scrollBottom(this.content);
        },
        addMessage(){

            let regEx =  /^[^\s]+/ ,   // /^_?[a-z\.]\D|\d+?$/i,
                input = document.querySelector('.main_input');  
            
            let _obj  = {
                text:input.value == '' ? this.text : input.value,
                name:this.name,
                createdAt:util.getNow()
            }       
            if(this.text && regEx.test(this.text) || (input.value !== '' && regEx.test(input.value) )){
                db.collection('chats').add(_obj);
                this.scroll();
                this.text = '';
            }
            else
                util.Handler('Invalid Value:Field must not be empty , and can`t start and end with space!');
            
        },
        deleteAllData(){

          let delete_message = db.collection('chats').where('name','==', this.name);
     

          delete_message.get().then(function(querySnapshot) {
              
            querySnapshot.forEach(function(doc) {
                doc.ref.delete();
                 return util.Handler('Your messages was deleted !' , false);
            });

          });
            
        },
    
    },

 
}
</script>

<style lang="scss" >
.flex-input{
    display: flex;
}
::-webkit-scrollbar {
    width: 10px;
}
 
::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background:#d1c4e9; 
}

.btn-group{
    flex-wrap: wrap;
    display: flex;
    min-width: 100%;
    margin-top: 0.5rem;
    .submit{flex-basis: 70%;}
    .delete{flex-basis: 30%;}
    .submit , .delete{
         position: relative;
        .prefix{top: 0; font-size: 1.5rem;}
    }
}


</style>



