<template>
	<div class="container">
		<div class="card">
			<div class="card-action">
				<h4 class="center orange-text text-darken-3">Aslanyan`s Chat</h4>
				<hr>
				<change-name :name="name" :counts="chatInfo" />
				<div class="card-content" ref="content">
					<span></span>
					<ul class="messages">
	
						<li v-for="(chat , index) in chats" :key="index"
								:class="name == chat.name ? 'current delete' : 'another' " @click.self="deleteData">

							<input type="hidden" name="hidden_name" :value="name" v-if="name == chat.name">
							<span class="name red-text text-darken-2">{{ name == chat.name ? 'You' :  chat.name}}</span>
							<p class="message aqua-text" ref="input" >{{chat.text}}</p>
							<span class="time cyan-text lighten-4">{{chat.createdAt.time}}</span>
							<span class="date cyan-text lighten-4">{{chat.createdAt.date}}</span>
				 
						</li>
	
					</ul>
				</div>
				<new-message :name="name" :content="this.$refs['content']" :issetMessage="chatInfo.curentMessage" />
			</div>
		</div>
	</div>
</template>

<script>
import NewMessage from '@/components/NewMessage';
import ChangeName from '@/components/ChangeName'
import db from '../firebase/init.js';
import util from '../utils/util';

/**
 * Creatd By @author Robert Aslanyan
 * May 2018 
 *  
 */
export default {
		components:{
			NewMessage,
			ChangeName
		},
		data(){
			return{
				name:document.cookie.split('=')[1],
				chatInfo:{
					allMessage:0,
					curentMessage:0,
					usersCount:0,
				},
				
			}
		},
		firestore() {
			return {
				chats: db.collection('chats').orderBy('createdAt')
								
			}
		},
		methods:{
			/**
			 * Delete Current elemnt data
			 * @augments Event
			 */
			deleteData(e){
				let currentEl = e.target; // Element in whcih was clicked
				
				if(currentEl.classList[0] == 'current'){ 
						let name = currentEl.firstChild.value; // Get name from hidden input 
						let text = currentEl.childNodes[2].textContent; // Get Text 
						

						/** Find in db by name then compare text and if equal , delete it */
						db.collection('chats').where('name' , '==' , name).get().then(prom => {
								prom.forEach(el => {
										if(el.data().text == text){
												util.Handler('Your Message was deleted!' , false , 2000);
												el.ref.delete();
										}
								});
						})
				}
			}

		},
		updated(){
		
			 let messages  = document.querySelectorAll('.message');
			 let regExp = /https?:(\/\/)(\w+)\.(\w{2,10})(\.\w{2,10})?\/?/i
			 let arr = [];
						

			 // Link text , if the text match to regExp
			 messages.forEach(el => {

					let text = el.textContent;

					if(text.match(regExp)){
						let link = '', // To Contain Links 
								simpleText = ''; // to Contain Text
						 let a = document.createElement('a');
						 a.setAttribute('target' , '_blank');

						 //el.textContent = ''; // Clear From Text 	

						let index = text.indexOf('h') !== -1 ? text.indexOf('h') : text.indexOf('H') ; // Find index of char  'h'
						let space_index = text.indexOf(' '); // Find index of space from begin
						let space_index_end = text.lastIndexOf(' '); // Find index of space from end

						if(index == 0){
								/** Only link */
								if(space_index === -1){
										 link = text; 
										 el.appendChild(a);
								}
								/** Link and some text in right side */
								else{ 
									link = text.slice(0 , space_index); 
									simpleText = document.createTextNode(text.slice(space_index));
									util.insertTo(el , a , simpleText);
								}
						}
						/** Link and some text in left side */
						else if(space_index === space_index_end){
							link = text.slice(index);
							simpleText = document.createTextNode(text.slice(0 , index));
							util.insertTo(el , simpleText , a);
						}
						/** Link with text from left and right side */
						else{
							link = text.slice(space_index , space_index_end);
							simpleText = document.createTextNode( text.slice(0 , space_index));
							util.insertTo(el , simpleText , a);
							simpleText = document.createTextNode(text.slice(space_index_end));
							el.appendChild(simpleText);
						}

						a.setAttribute('href' , link); // Set Attribute 

						return a.textContent = `${link}`; // Return link 

					}
				 
			 });

			/** Request For Current User  */
			db.collection('chats').get()
					.then(data => {
							let curent = [];
							data.forEach(el => {
								if(el.data().name == this.name)
									return curent.push(this.name);
							});
					return this.chatInfo.curentMessage = curent.length;
			  });

            /** Request For All Users  */
			db.collection('chats').get()
					.then(data => {
							let all = [];
							data.forEach(el => all.push(el.ref.id));

					return this.chatInfo.allMessage = all.length;
			});

			db.collection('chats').get()
					.then(data => {
							let users = [];
							data.forEach(el => users.push(el.data().name));

						/** Make array unique */
				users = users.filter((el , index ,arr) =>  arr.indexOf(el) == index)


				return this.chatInfo.usersCount  = users.length;
			});

		    /** Emoji Parser */
			util.emojiParser(messages);
	
		}


}
</script>

<style lang="scss" scoped>

.card-content{
	max-height: 60vh;
	overflow-y: scroll;
	overflow-x: hidden;
	position: relative;
	z-index: 1;
}
.name{
		text-transform: capitalize;
		margin-right: 0.5rem;
	}
.message{
	word-break: break-all;
	font-weight: 600;
	padding: .5rem;
}
.time , .date{display: block; text-align: right;}
.date{ font-size: .75rem;}
.messages{ 
	margin-top: .5rem;
	li{
		margin-top: 1rem;
		padding: 0.5rem;
		border-radius: 10px;
		position: relative;
		&:after{
			content: '';
			position: absolute;
			top: calc(50% - 8px);
			transform: rotate(45deg);
			z-index: -1;
			height: 16px;
			width: 16px;
			background: inherit;
		}
	}
	.current , .another{
		clear:both;
	}
	.current{
		background: #ede7f6;
		float: right;
		&:after{ right: -8px; }
	}
	.another{
		background:#bbdefb;
		float: left;
		&:after{left:-8px;}

	}
}
.delete{
	position: relative;
	cursor: pointer;
	overflow: hidden;
	&:hover  {
			transform: scale(1.1);
			&:before{
			font-family: 'Material Icons';
			content: 'delete';
			font-size: 2rem;
			color:#fff;
			text-align: center;
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 50%;
			background:rgba(#333 , .65);
		}
	}
}
</style>




